<template>
	<div ref="wrapper">
		<slot></slot>
	</div>
</template>
<script type="text/ecmascript-6">
	import BScroll from 'better-scroll'
	/*betterScroll*/
	export default {
		created(){
			
			this.cnsole()
		},
		props:{
			probeTable:{
				type:Number,
				default:1
			},
			click:{
				type:Boolean,
				default:true
			},
			data:{
				type:Array,
				default: null
			},
			listenScroll:{/**/
				type:Boolean,
				default:false
			},
			probeType: {//3 onscroll
        type: Number,
        default: 1
      },
			puallup:{
				type:Boolean,
				default:false
			},
			beforeScroll:{
				type:Boolean,
				default:true
			},
			refreshDelay:{
				type:Number,
				default: 20
			}
		},
		mounted(){
			setTimeout(()=>{
				this._initScrpll()
			},20)

		},
		methods:{
			cnsole(){
				console.log(this.data)
				
			},
			_initScrpll(){
				
				if(!this.$refs.wrapper){
					return
				}
				this.scroll = new	BScroll(this.$refs.wrapper,{
					probeType: this.probeType,
					click: this.click
				})
				if(this.listenScroll){
					let me = this 
					this.scroll.on('scroll',(pos)=>{//	probeType: {//3 onscroll pos
						//scrollx y 
						me.$emit('scroll',pos)
					})
				}	
				if(this.puallup){
					this.scroll.on('scrollEnd',()=>{
						 if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              this.$emit('scrollToEnd')
            }
					})
				}
				if(this.beforeScroll){
					this.scroll.on('beforeScrollStart',()=>{
						this.$emit('beforeScroll')
					})
				}
			},
			enable(){
				this.scroll && this.scroll.enable()/* better-scroll,  */
			},
			disable(){
				this.scroll && this.scroll.disable()/* better-scrollDOM  touchstarttouchmovetouchend*/
			},
			refresh(){
				this.scroll && this.scroll.refresh()/* better-scroll DOM */
			},
			scrollTo(){
				this.scroll && this.scroll.scrollTo.apply(this.scroll,arguments)
			},
			scrollToElement() {
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)/*betterscroll
				*/
      },
			
		},
		watch:{/*data*/
			data(){
				setTimeout(()=>{
					this.refresh()
				},this.refreshDelay)
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus"></style>


